<template>
  <div class="part-two" id="part-two"></div>
</template>

<script>
// 
let echarts = require("echarts/lib/echarts");

// 
require("echarts/map/js/china.js");

export default {
  name: "partTwo",
  data() {
    return {};
  },
  mounted() {
    this.drawECharts();
  },
  methods: {
    drawECharts() {

      // domecharts
      let myChart = echarts.init(document.getElementById("part-two"));

      // 
      let myFirendCity = [
        { name: "", value: ["113.23", "23.16", "9"] },
        { name: "", value: ["114.07", "22.62", "12"] },
        { name: "", value: ["121.48", "31.22", "10"] },
        { name: "", value: ["108.95", "34.27", "4"] },
        { name: "", value: ["116.46", "39.92", "12"] },
      ];

      // 
      let myFriendProvince = [
        { name: "", value: 1 },
        { name: "", value: 1 },
        { name: "", value: 21 },
        { name: "", value: 1 },
        { name: "", value: 12 },
        { name: "", value: 1 },
        { name: "", value: 10 },
        { name: "", value: 4 },
        { name: "", value: 1 },
        { name: "", value: 1 },
        { name: "", value: 1 },
        { name: "", value: 2 },
        { name: "", value: 1 },
        { name: "", value: 3 },
        { name: "", value: 1 },
        { name: "", value: 1 }
      ];

      myChart.setOption({
        // 
        title: {
          text: "",
          textStyle: {
            color: "#fff"
          },
          subtext: "",
          subtextStyle: {
            color: "#fff"
          },
          x: "center"
        },
        // 
        tooltip: {
          trigger: "item",
          // 
          formatter: function(params) {
            if (params.value[2] == undefined) {
              if(!params.name) {
                return "";
              } else {
                return params.name + " : " + params.value;
              }
            } else {
              return params.name + " : " + params.value[2];
            }
          }
        },
        // 
        visualMap: {
          text: ["", ""],
          min: 0,
          max: 30,
          // 
          calculable: true,
          inRange: {
            color: ["#e4e004", "#ff5506", "#ff0000"]
          },
          textStyle: {
            color: "#fff"
          }
        },
        // geo
        geo: {
          map: "china"
        },
        // 
        series: [
          // 
          {
            name: "",
            type: "effectScatter",
            coordinateSystem: "geo",
            symbolSize: function(val) {
              return val[2] * 2;
            },
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke"
            },
            hoverAnimation: true,
            label: {
              normal: {
                formatter: "{b}",
                position: "right",
                show: true,
                color: "#fff"
              }
            },
            itemStyle: {
              normal: {
                color: "#ddb926",
                shadowBlur: 10,
                shadowColor: "#333"
              }
            },
            //  z-index
            zlevel: 1,
            data: myFirendCity,
          },
          // 
          {
            name: "",
            type: "map",
            mapType: "china",
            // 
            roam: false,
            label: {
              // 
              normal: {
                formatter: myFirendCity,
                show: false,
                textStyle: {
                  color: "#fff"
                }
              },
              // 
              emphasis: {
                show: false
              }
            },
            itemStyle: {
              normal: {
                borderWidth: 0.5, // 
                borderColor: "#fff", // 
                areaColor: "deepskyblue" // 
              },
              // 
              emphasis: {
                borderWidth: 1,
                borderColor: "#fff",
                areaColor: "#00aeff"
              }
            },
            // 
            data: myFriendProvince
          }
        ]
      });
    }
  }
};
</script>

<style scoped>
.part-two {
  width: 100%;
  height: 500px;
  border: 40px solid transparent;
  border-image: url("~@/./assets/img/border_image.png") 30 30 stretch;
  background: #18202d;
}
</style>